<script>
export default {
  data () {
    return {
    }
  },
  computed: {
    location() {
      return this.$store.getters['getlocationInfo']
    }
  },
  created() {
    let itemid = this.$route.query.itemid;
    let orderid = this.$route.query.orderid;
    let orderprice = this.$route.query.orderprice;
    let storeId = this.$route.query.storeId;
    let streetId = this.location.townshipId||'12036597';
    let NTKF_PARAM = {
      siteid: 'sg_1000', // 企业id
      settingid: 'sg_1000_template_9999', // app 客服组ID
      uid: localStorage.getItem('mId'), // 用户id，登录用户必填，游客为空字符串
