      userlevel: 0, // 网站用户级别，登录用户可选，游客不填,
      erpparam: '' // erpparam为erp功能的扩展字段，可选，购买erp功能后用于erp功能集成
    }

    if (itemid != null && itemid !== '') {
      NTKF_PARAM.itemid = itemid;
      NTKF_PARAM.itemparam = storeId+','+streetId;
    }
